.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  font-family: "Kanit", sans-serif;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

.loaded .preloader {
  display: none;
}

.preloader {
  /* height: 100vh; */
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("preloader.gif");
  z-index: 20;
}
.loaded_hiding .preloader {
  transition: 3s all;
  opacity: 0;
}

.loaded_hiding .preloader {
  transition: 3s all;
  opacity: 0;
}

.loaded .preloader {
  display: none;
}

.navbar {
}
.score {
  font-size: 30px;
}
.card__container {
  display: flex;
  justify-content: space-around;
  position: relative;
  width: 23vw;
}

.card {
  background-repeat: no-repeat;
  background-size: contain;
  height: 160px;
  width: 112px;
  background-position: center;
border-radius: 5px;
border-radius: 4px;
transition: .3s all;
background-color: white;


}
.card__container div:hover {
  box-shadow: 5px 5px 10px grey;
  z-index: 10;

  cursor: pointer;
  transition: .3s all;
}
.card__container div:active {
  background-size: 95% 95%;
  transition: .3s all;
}
.tuz {
  background-image: url(./images/Tuz.png);
  transform: rotate(-10deg);
  position: absolute;
  left: -60px;
  bottom: -14px;
}
.teen {
  background-image: url(./images/10.png);
  transform: rotate(-4deg);
}
.king {
  background-image: url(./images/King.png);
  position: absolute;
  top: -5px;
}
.queen {
  background-image: url(./images/Queen.png);
  transform: rotate(4deg);
}
.valet {
  background-image: url(./images/Valet.png);
  transform: rotate(10deg);
  position: absolute;
  right: -60px;
  bottom: -14px;
}

.button {
  margin-top: 5%;
  width: 140px;
  height: 30px;
  background-color: black;
  color: white;
  border-radius: 19px;
  cursor: pointer;
}
.button:hover {
  background-color: #282c34;
}

.button:active {
  background-color: #282c34db;
}

@media (max-width: 900px) {
  .card__container {
    display: flex;
    flex-direction: column;

    justify-content: space-around;
    align-items: center;
    height: 74vh;
}
.card__container div:hover {
  box-shadow: none;

  cursor: pointer;
}
.card__container div:active {
  background-size: 80% !important;
}
.card {
  width: 80px;
}
.tuz {
  position: relative;
  transform: rotate(0);
  right: 0;
  bottom: 0;
  left: 0;

}
.teen {
  position: relative;
  transform: rotate(0);
  right: 0;
  bottom: 0;

}
.king {
  position: relative;
  transform: rotate(0);
  right: 0;
  bottom: 0;

}
.queen {
  position: relative;
  transform: rotate(0);
  right: 0;
  bottom: 0;

}
.valet {
  position: relative;
  transform: rotate(0);
  right: 0;
  bottom: 0;
}
}